.mealplan {
    text-align: center;
    min-height: 100vh;
}

.meal-interface {
    display: flex;
    justify-content: center;
    flex-direction: column;
    max-width: 100vw;
    width: 100%;
}

.container {
    display: flex;
    justify-content: center;
}

.left-container,
.right-container {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    border: 1px solid white;
    border-radius: 10px;
    background-color: #444444; /* manually lightened color */
    padding: 10px;
    margin: 10px;
    box-sizing: border-box;
}

.navigate-container {
    display: flex;
    justify-content: center;
    background-color: #55555555;
    border: 10px;
    margin: 10px auto;
    border-radius: 10px;
    width: 75%;
}

.BASIC, .ADVANCED {
    flex: 1;
    padding: 10px;
    margin-right: 50px;
    margin-left: 50px;
    text-align: center;
    border: 1px solid black;
    border-radius: 5px;
}

.calorie-tracker {
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: space-between;
    padding: 5px;
    box-sizing: border-box;
}

.calorie-input, .goal-input {
    flex: 1;
    margin: 5px;
    border: 1px solid black;
    border-radius: 5px;
    text-align: center;
}

.spending-input, .budget-input {
    flex: 1;
    margin: 5px;
    border: 1px solid black;
    border-radius: 5px;
    text-align: center;
}

.reset-goal-button {
    color: #2761ac;
    background-image: url('../images/reset.png');
    background-size: 50%;
    background-position: center;
    background-repeat: no-repeat;
    width: 25px;
    height: 25px;
    border: none;
    cursor: pointer;
}

.reset-goal-button:hover {
    opacity: 0.8;
}

.row1, .row2 {
    display: flex;
}

.calorie-graph canvas {
    border: 2px solid red;
    border-radius: 10px;
}

.calorie-graph .chartjs-render-monitor {
    color: red;
}